/* About Section */
#about {
    height: 100vh;
    padding: 0 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: #ffffff;
    scroll-snap-align: start;
    width: 100%;
}

.about-content {
    display: flex;
    flex-direction: row;
    column-gap: 750px; /* Increased gap to position columns further apart */
    width: 100%;
    max-width: 1800px;
    justify-content: space-between;
    align-items: stretch;
    opacity: 0; /* Initially hidden */
    transform: translateY(20px); /* Initial downward offset */
    transition: opacity 1s ease-in, transform 1s ease-in; /* Transition for easing in */
}

#about.visible .about-content {
    opacity: 1; /* Fully visible when scrolled to */
    transform: translateY(0); /* Move to normal position */
}

.experience, .tech-stack {
    flex: 1;
    background-color: transparent; /* Transparent background */
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
    display: flex;
    flex-direction: column;
}

.experience {
    align-items: flex-start; /* Align experience column to the left */
}

.tech-stack {
    align-items: flex-end; /* Align tech stack column to the right */
}

.experience h3, .tech-category h3 {
    font-size: 2rem; /* Ensured both have the same size */
    color: #2ec1ff; /* Updated title color */
    margin-bottom: 20px;
}

.experience-item {
    margin-bottom: 20px;
    text-align: left;
    opacity: 0; /* Initially hidden */
    transform: translateY(20px); /* Initial downward offset */
    transition: opacity 1s ease-in, transform 1s ease-in; /* Transition for easing in */
}

#about.visible .experience-item {
    opacity: 1; /* Fully visible when scrolled to */
    transform: translateY(0); /* Move to normal position */
}

.experience-item h4 {
    font-size: 1.4rem;
    color: #2ec1ff; /* Updated title color */
    margin-bottom: 5px;
    font-weight: bold;
}

.experience-meta {
    display: flex;
    justify-content: space-between;
    font-size: 0.8rem; /* Reduced font size */
    color: #c9c9c9;
    margin: 5px 0;
    line-height: 1.5;
    font-style: italic;
    opacity: 0; /* Initially hidden */
    transform: translateY(20px); /* Initial downward offset */
    transition: opacity 1s ease-in, transform 1s ease-in; /* Transition for easing in */
}

#about.visible .experience-meta {
    opacity: 1; /* Fully visible when scrolled to */
    transform: translateY(0); /* Move to normal position */
}

.location {
    text-align: left;
}

.date {
    text-align: right;
}

.experience-item p {
    font-size: 1rem;
    color: #ffffff; /* Keep description text white */
    margin: 5px 0;
    line-height: 1.5;
}

.tech-category {
    margin-bottom: 20px;
    width: 100%;
    text-align: center; /* Center align category titles */
}

.tech-category h3 {
    font-size: 2rem; /* Ensured both experience and tech stack titles have the same size */
    color: #2ec1ff; /* Updated title color */
    margin-bottom: 10px;
}

.tech-icons {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    justify-content: center; /* Center align icons */
    opacity: 0; /* Initially hidden */
    transform: translateY(20px); /* Initial downward offset */
    transition: opacity 1s ease-in, transform 1s ease-in; /* Transition for easing in */
}

#about.visible .tech-icons {
    opacity: 1; /* Fully visible when scrolled to */
    transform: translateY(0); /* Move to normal position */
}

.tech-icon {
    font-size: 3.5rem; /* Enlarged by 30% from the original size of 3rem */
    color: inherit; /* Ensure the icons retain their original color */
}

@media (max-width: 768px) {
    
    /* About Section */
    #about {
        width: 100vw; /* Full viewport width */
        height: 100vh; /* Full viewport height */
        padding: 20px; /* Padding for consistent spacing */
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        color: #ffffff;
        scroll-snap-align: start;
        box-sizing: border-box;
        overflow-x: hidden; /* Prevent horizontal scrolling */
        background-size: cover; /* Ensure background spans entire section */
        background-position: center; /* Center background for consistency */
    }

    .about-content {
        display: flex;
        flex-direction: column; /* Stack sections vertically */
        align-items: center; /* Center align all items */
        gap: 40px; /* Space between sections for readability */
        width: 100%;
        max-width: 1000px; /* Limit max width for readability on large screens */
        margin: 0;
        padding: 0;
        box-sizing: border-box;
    }

    .experience, .tech-stack {
        width: 90%; /* Take most of the screen width */
        padding: 20px; /* Padding for visual separation */
        background-color: rgba(0, 0, 0, 0.7); /* Background for readability */
        border-radius: 15px; /* Rounded corners for visual appeal */
        box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3); /* Subtle shadow for depth */
    }

    .experience h3, .tech-category h3 {
        font-size: 1.8rem; /* Consistent font size for headings */
        color: #2ec1ff; /* Accent color for titles */
        margin-bottom: 20px; /* Space below heading */
    }

    .experience-item {
        width: 100%;
        text-align: left; /* Align text for easier reading */
        margin-bottom: 20px; /* Space between items */
    }

    .experience-item h4 {
        font-size: 1.4rem; /* Adjusted for readability on mobile */
        color: #2ec1ff; /* Accent color for job titles */
        font-weight: bold;
        margin-bottom: 10px;
    }

    .experience-meta {
        display: flex;
        justify-content: space-between; /* Place date and location at ends */
        font-size: 1rem; /* Larger for readability */
        color: #c9c9c9; /* Light color for metadata */
        font-style: italic;
        margin-bottom: 10px;
    }

    .experience-item p {
        font-size: 1.1rem; /* Slightly larger for mobile readability */
        color: #ffffff;
        line-height: 1.6; /* Improve readability */
        margin-bottom: 15px;
    }

    .tech-stack {
        display: flex;
        flex-direction: column; /* Stack tech stack categories */
        align-items: center; /* Center align all items */
        gap: 30px; /* Space between categories */
    }

    .tech-category {
        width: 100%; /* Full width for alignment */
        text-align: center;
        margin-bottom: 15px;
    }

    .tech-category h3 {
        font-size: 1.8rem; /* Consistent with experience titles */
        color: #2ec1ff;
        margin-bottom: 15px;
    }

    .tech-icons {
        display: flex;
        flex-wrap: wrap; /* Allow wrapping for better fit */
        gap: 15px; /* Space between icons */
        justify-content: center; /* Center align the icons */
    }

    .tech-icon {
        font-size: 2.8rem; /* Increase size for visual clarity on mobile */
        color: whitesmoke; /* Accent color for consistency */
    }
}

@media (min-width: 1600px) {
    /* Your CSS rules for ultrawide screens */
    .experience {
        transform: translateX(-50%);
    }
    .tech-stack {
        transform: translateX(50%);
    }
  }
  