body {
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    scroll-behavior: smooth; /* Enable smooth scrolling for entire page */
}

.side-navigation {
    position: fixed;
    left: 20px;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    gap: 20px;
    z-index: 1000; /* Ensure it stays above other content */
}

.nav-blob {
    width: 10px;
    height: 30px; /* Elongated shape for more prominence */
    background-color: #888; /* Default color for inactive blobs */
    border-radius: 20px; /* Makes the blob a capsule shape */
    cursor: pointer;
    transition: all 0.3s ease;
}

.nav-blob:hover {
    width: 12px;
    height: 36px;
    background-color: #2ec1ff; /* Accent color on hover */
}

.nav-blob.active {
    width: 12px;
    height: 36px;
    background-color: #2ec1ff; /* Accent color for the active section */
    box-shadow: 0 0 10px rgba(106, 221, 254, 0.8); /* Adds a subtle glow to the active blob */
}

@media (max-width: 768px) {
    .side-navigation {
        left: 10px; /* Adjusted to keep side navigation within viewport */
        gap: 15px;
    }

    .nav-blob {
        width: 12px;
        height: 36px; /* Reduced size for mobile */
    }

    .nav-blob.active, .nav-blob:hover {
        width: 14px;
        height: 40px;
    }
}
