/* Landing Page Section */
#landing {
    position: relative;
    width: 100%;
    height: 100vh; /* Full viewport height */
    background: black; /* Full-width background image */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.landing-content {
    text-align: center;
    color: #ffffff; /* Light text for readability */
    z-index: 2; /* Ensure text is above the background */
}

.landing-name {
    font-size: 5rem; /* Larger size for emphasis */
    font-weight: bold;
    margin: -30px;
}

.landing-position {
    font-size: 2rem; /* Large but smaller than the name */
    margin-top: 20px;
    color: #ffffff; /* Gold accent color for contrast */
}

.landing-navigation {
    position: absolute;
    bottom: 20px;
    text-align: center;
    cursor: pointer;
}


.scroll-icon {
    font-size: 2.5rem;
    color: #ffffff;
    transition: color 0.3s ease, transform 0.3s ease;
}

.scroll-icon:hover {
    color: #2ec1ff;
    transform: translateY(10px); /* Subtle animation for emphasis */
}

.landing-links {
    position: absolute;
    bottom: 410px;
    display: flex;
    gap: 40px;
    color: #ffffff;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 1.2rem;
}

.landing-links a {
    color: #ffffff;
    text-decoration: none;
    transition: color 0.3s ease;
}

.landing-links a:hover {
    color: #2ec1ff;
}

/* Logo and Social Links Section */
.logo-social-links {
    position: absolute;
    bottom: 20px;
    left: 20px;
    display: flex;
    align-items: center;
    gap: 20px;
}

.landing-logo-bottom {
    width: 40px;
    height: 40px;
}

.social-icon {
    font-size: 2rem;
    color: #ffffff;
    transition: color 0.3s ease;
}

.social-icon:hover {
    color: #2ec1ff;
}

/* Copyright Section */
.copyright-text {
    position: absolute;
    bottom: 20px;
    right: 20px;
    color: #ffffff;
    font-size: 1rem;
    opacity: 0.8; /* Slight transparency for a subtle look */
}

@media (max-width: 768px) {
    .landing-name {
        font-size: 3rem; /* Adjusted font size for smaller screens */
        margin: 0;
    }

    .landing-position {
        font-size: 1.5rem; /* Adjusted font size */
        margin-top: 15px;
        margin-bottom: 20px; /* Add margin below to create space for landing-links */
    }

    .landing-links {
        position: absolute;
        bottom: 110px; /* Same as .logo-social-links to align vertically */
        right: 60px; /* Align to the bottom right corner */
        gap: 20px; /* Reduced gap for better spacing on mobile */
        display: flex;
        flex-direction: row;
        align-items: flex-end; /* Align links to the right */
    }

    .logo-social-links {
        left: 10px;
        bottom: 100px; /* Increased bottom value to move upward */
        gap: 15px; /* Reduced gap for better spacing on mobile */
    }

    .copyright-text {
        position: absolute;
        bottom: 10px;
        left: 50%;
        transform: translateX(-50%); /* Center align the copyright text */
        width: 100%;
    }
}




