/* Projects Styles */
body {
    overflow-x: hidden;
    margin: 0;
    padding: 0;
}

#projects {
    width: 100vw;
    height: 100vh;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    color: #ffffff;
    box-sizing: border-box;
    background: url('../../public/assets/images/redsun.gif') center/cover no-repeat; /* Background image */
    user-select: none; /* Prevent text selection */
}

.projects-heading {
    font-size: 3rem;
    margin-bottom: 20px;
    color: #1c8dff; /* Blue color to match the provided GIF */
    user-select: none; /* Prevent text selection */
}

.projects-page {
    display: flex;
    justify-content: center;
    gap: 50px; /* Spaced cards out a bit more */
    margin-bottom: 30px;
    width: 100%;
    position: relative; /* Set to relative to allow fading animations */
    transition: opacity 0.5s ease-in-out; /* Fade transition */
}

.fade-out {
    opacity: 0;
}

.fade-in {
    opacity: 1;
}

.project-card {
    background-color: #0e101c; /* Darker background to complement the blue theme */
    border-radius: 10px;
    padding: 20px;
    cursor: pointer;
    text-align: center;
    color: #f1f1f1;
    width: 360px; /* Increased card width slightly */
    height: 500px; /* Increased card height to elongate */
    box-sizing: border-box;
    user-select: none; /* Prevent text selection */
    transition: transform 0.6s ease-in-out, box-shadow 0.6s ease-in-out; /* Smooth floaty effect */
    animation: float 3s ease-in-out infinite; /* Idle floating animation */
    box-shadow: 0 0 30px 10px rgba(28, 141, 255, 0.3); /* Initial blue glow */
    display: flex;
    flex-direction: column;
    justify-content: space-between; /* Uniform button placement */
}

.project-card:hover {
    box-shadow: 0 0 50px 20px rgba(28, 141, 255, 0.6); /* More intense blue shadow on hover */
}

.project-card-img {
    width: 100%;
    height: 230px; /* Increased height for a better balance */
    object-fit: cover;
    border-radius: 10px;
    margin-bottom: 15px;
}

.project-card-title {
    font-size: 1.6rem; /* Adjusted to fit on one line */
    margin-bottom: 5px;
    color: #1c8dff; /* Blue color to match the provided GIF */
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.project-icons {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-bottom: 15px; /* Reduced spacing for better fit */
}

.tech-icon-proj {
    font-size: 1.8rem; /* Adjusted size slightly */
    color: #1c8dff; /* Blue color to match the provided GIF */
}

.project-links {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin-top: auto; /* Ensures uniform button placement across all cards */
}

.project-link-button {
    font-size: 0.9rem; /* Adjusted to fit text in one line */
    padding: 10px 25px; /* Adjusted padding for consistency and increased height */
    border: 2px solid #1c8dff;
    background: transparent;
    color: #1c8dff;
    cursor: pointer;
    border-radius: 5px;
    text-decoration: none;
    transition: background-color 0.3s ease, color 0.3s ease;
    user-select: none; /* Prevent text selection */
    white-space: nowrap; /* Ensures button text fits on one line */
}

.project-link-button:hover {
    background-color: #1c8dff;
    color: #333;
}

.page-indicator {
    display: flex;
    gap: 10px;
    justify-content: center;
    margin-top: 20px;
}

.page-dot {
    width: 15px;
    height: 15px;
    background-color: #555;
    border-radius: 50%;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.page-dot.active {
    background-color: #1c8dff; /* Highlighted color for active page */
}

.page-dot:hover {
    background-color: #777;
}

.nav-arrow {
    visibility: hidden;
}

@keyframes float {
    0%, 100% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-10px);
    }
}

@media (max-width: 768px) {
    #projects {
        width: 100vw; /* Full viewport width */
        padding: 20px; /* Padding for consistent spacing */
        box-sizing: border-box; /* Include padding and border in width */
        color: #ffffff;
        overflow-x: hidden; /* Prevent horizontal scrolling */
        text-align: center;
    }

    .projects-heading {
        font-size: 2rem; /* Adjusted font size for mobile */
        margin-bottom: 20px; /* Space below heading */
        color: #2ec1ff; /* Accent color for heading */
    }

    .projects-page {
        display: flex;
        flex-direction: column; /* Stack cards vertically */
        align-items: center; /* Center align the card */
        width: 100%;
    }

    .project-card {
        width: 100%; /* Full width to take up the screen */
        max-width: 400px; /* Limit card width to ensure consistency */
        padding: 20px; /* Padding for visual separation */
        background-color: #141313; /* Dark background color for the cards */
        border-radius: 10px; /* Rounded corners for aesthetics */
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3); /* Subtle shadow for depth */
        display: flex;
        flex-direction: column;
        align-items: center; /* Center align card content */
        margin-bottom: 20px; /* Reduced margin to ensure arrows and indicators are closer */
    }

    .project-card-img {
        width: 100%;
        height: auto; /* Keep aspect ratio */
        max-height: 250px; /* Limit height for mobile */
        object-fit: cover; /* Maintain aspect ratio without distortion */
        border-radius: 8px; /* Rounded corners for image */
        margin-bottom: 15px; /* Space below image */
    }

    .project-card-title {
        font-size: 1.6rem; /* Larger font for readability */
        color: #2ec1ff; /* Accent color for titles */
        margin-bottom: 10px; /* Space below title */
    }

    .tech-icons-proj {
        display: flex;
        flex-wrap: wrap; /* Allow wrapping for better fit */
        gap: 10px; /* Space between icons */
        justify-content: center; /* Center align the icons */
        margin-bottom: 15px; /* Space below icons */
    }

    .project-link-button {
        width: 100%; /* Full width of card to make the button prominent */
        padding: 12px; /* Padding for better tap targets */
        font-size: 1rem; /* Adjusted for readability */
        color: #ffffff;
        background-color: #05aef7af; /* Match theme color */
        border: none;
        border-radius: 8px; /* Rounded corners for consistency */
        cursor: pointer;
        transition: background-color 0.3s ease;
    }

    .project-link-button:hover {
        background-color: #1b89b856; /* Darker color on hover for visual feedback */
    }

    .page-navigation {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 10px; /* Reduced space between arrows and dots for better positioning */
        margin-top: 10px; /* Reduced margin to bring navigation closer to the card */
    }

    .nav-arrow {
        font-size: 1.8rem; /* Size for visibility */
        color: whitesmoke;
        cursor: pointer;
        transition: color 0.3s ease;
        visibility: visible;
        margin-top: -60px;
    }

    .nav-arrow.disabled {
        color: #555; /* Dim the color when disabled */
        cursor: not-allowed; /* Show disabled cursor */
    }

    .page-indicator {
        display: flex;
        gap: 8px; /* Reduced gap for better spacing */
        margin-top: -60px; /* Move indicator closer to arrows */
    }

    .page-dot {
        width: 12px;
        height: 12px;
        background-color: #c9c9c9; /* Default color */
        border-radius: 50%; /* Make it circular */
        cursor: pointer;
    }

    .page-dot.active {
        background-color: #2ec1ff; /* Highlight active page */
    }

    .tech-icon-proj {
        font-size: 3rem;
        color: whitesmoke;
    }
}





