@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap');

body {
    font-family: 'Poppins', sans-serif; /* Modern and easy to read font */
    line-height: 1.6;
    margin: 0;
    scroll-snap-type: y mandatory; /* Snap scrolling */
    overflow-y: scroll;
    min-height: 100vh; /* Ensure body covers the full viewport */
}

#wrapper {
    min-height: 100vh; /* Ensure wrapper covers the full viewport */
    display: flex;
    flex-direction: column;
}

#landing, #about, #projects {
    scroll-snap-align: start;
    min-height: 100vh; /* Each section fills the viewport */
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

html {
    scroll-behavior: smooth;
}

body, html {
    overflow-x: hidden; /* Prevent horizontal scrolling */
    margin: 0; /* Remove default margin */
    padding: 0; /* Remove any default padding */
    height: 100%; /* Ensure body and html occupy full height of viewport */
}

.snap-container {
    scroll-snap-type: y mandatory; /* Ensures sections snap into place when scrolling */
}

@media (max-width: 768px) {
    body {
        font-family: 'Poppins', sans-serif; /* Maintain modern and easy-to-read font */
        scroll-snap-type: y mandatory; /* Enable snap scrolling for vertical sections */
        scroll-behavior: smooth; /* Enable smooth scrolling throughout the page */
        line-height: 1.6;
        margin: 0;
        padding: 0;
        overflow-x: hidden;
        overflow-y: scroll; /* Allow vertical scrolling */
    }

    #landing, #about, #projects {
        scroll-snap-align: start; /* Snap each section into view */
        min-height: 100vh; /* Each section fills the full viewport height */
        width: 100%;
        display: flex;
        flex-direction: column; /* Ensure consistent column layout */
        align-items: center; /* Center align content */
        justify-content: center; /* Center content vertically */
        padding: 20px; /* Padding for consistency */
        box-sizing: border-box; /* Include padding and borders in size */
    }

}


